import * as Yup from 'yup';
import { useState } from 'react';
// form
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers/yup';
import { useSnackbar } from 'notistack';
// @mui
import { LoadingButton } from '@mui/lab';
import { Box, Card, Grid, Alert, Stack, Tab, Tabs} from '@mui/material';
import axios from '../../utils/axios';
import { FormProvider,   RHFTextField, RHFEditor } from '../../components/hook-form';
import UserSearch from './UserSearch';
import Scrollbar from '../../components/Scrollbar';
import AdexUser from './AdexUser';
// drop dwon style
import useIsMountedRef from '../../hooks/useIsMountedRef';

// ----------------------------------------------------------------------

export default function System() {
    const { enqueueSnackbar } = useSnackbar();
    const isMountedRef = useIsMountedRef();
    const [status, setStatus] = useState('ALL');
  const NewUserSchema = Yup.object().shape({
title: Yup.string().required(),
message: Yup.string().required(),
 });

  
  const AccessToken =  window.localStorage.getItem('accessToken');
  const [userUsername, SetUserUsername] = useState('');
  const [searchResults, setSearchResults] = useState([]);
  const [isSearchFocused, setSearchFocused] = useState(false);
  const displayResults = userUsername && isSearchFocused;
  const methods = useForm({
    resolver: yupResolver(NewUserSchema),
  });
  const ACCOUNT_TABS = [
    {
     value: 'ALL',
     label: 'ALL USERS'
    },
    {
      value: 'SMART',
      label: 'SMART USER'   
    },
    {
      value: 'AWUF',
     label: 'AWUF USERS'
    },
    {
        value: 'API',
        label: 'API USERS'
    },
    {
        value: 'SPECIAL',
        label: 'SPECIAL USERS'
    },
    {
        value: 'CUSTOM',
        label: 'CUSTOM'
    }
  ];
  const {
    setError,
    handleSubmit,
    formState: {errors, isSubmitting },
  } = methods;
 
  const onSubmit = async (data) => {
    try {
     await axios.post(`/api/gmail/sendmessage/${AccessToken}/adex/secure`,{
        status,
        title: data.title,
        message: data.message,
        user_username: userUsername
     })
     enqueueSnackbar('message sent');
    } catch (error) {
        if (isMountedRef.current){
            setError('afterSubmit', error);
          }
    }
  };
  const handleSearchFocus = () => {
    setSearchFocused(true);
  };
  const handleChangeSearch = async (event) => {
  const { value } = event.target;
      SetUserUsername(value);
      if (value) {
      try {
       const response = await axios.post(`/api/filter/user/details/admin/by/adex/${AccessToken}/secure/react`,{
        username: value
       });
     setSearchResults(response.data.user);
    }catch(error){
     console.log(error)
    }  
      } else {
        setSearchResults([]);
      }

  };
   
  return (
    <FormProvider methods={methods} onSubmit={handleSubmit(onSubmit)}>
      <Grid container spacing={3}>
        <Grid item >
        {!!errors.afterSubmit && <Alert severity="error">{errors.afterSubmit.message}</Alert>}
          <Card sx={{ p: 3 }}>
     
            <Box
              sx={{
                display: 'grid',
                columnGap: 3,
                rowGap: 3,
                gridTemplateColumns: { xs: 'repeat(1, 1fr)', sm: 'repeat(1, 1fr)' },
              }}
            > <Tabs
            value={status}
            scrollButtons="auto"
            variant="scrollable"
            allowScrollButtonsMobile
            onChange={(e, value) => setStatus(value)}
          >
            {ACCOUNT_TABS.map((tab) => (
              <Tab disableRipple key={tab.value} label={tab.label} value={tab.value} />
            ))}
          </Tabs>  
        {status === 'CUSTOM' && <UserSearch
          query = {userUsername}
          onChange = {handleChangeSearch}
          onFocus = {handleSearchFocus}
        />  }
         {displayResults &&
           <Scrollbar>
           <AdexUser  query={userUsername} results={searchResults} onSelectContact={(e)=>{
            SetUserUsername(e.username); setSearchFocused(false)
           }}/>
           </Scrollbar>}         
                 <RHFTextField name="title" label="Title" />
                 <RHFEditor name="message" label="Message"/>
             </Box>
            <Stack alignItems="flex-end" sx={{ mt: 3 }}>
              <LoadingButton  fullWidth size="large" type="submit" variant="contained" loading={isSubmitting}>
               Proceed
              </LoadingButton>
            </Stack>
          </Card>
        </Grid>
      </Grid>
    </FormProvider>
  );
}
